.container {
    text-align: center;
}

.card {
    width: 100%;
    max-width: 100%;  
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);  
    text-align: center;
    transition: height 2.8s ease, box-shadow 0.8s ease;
    margin-top: 20px;  
    overflow: hidden;
    height: 250px;
    font-size: 20px;
}

.card:hover {
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    height: 700px;
}
