.box-with-cut-corners {
  margin-top: 100px;
  height: 300px;
  
  position: relative;
  border: 3px solid rgba(1, 49, 185, 0.20);
}

.box-with-cut-corners::before,
.box-with-cut-corners::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  background-color: transparent;
  z-index: 2;
  
}

.box-with-cut-corners::before {
  /* top: -5px; */
  bottom: -5px;
  left: -5px;
  border-width: 0 50px 50px 0;
  border-left: 2px solid red;
  border-color: transparent transparent white transparent;
}

.box-with-cut-corners::after {
  /* bottom: -2px; */
  top: -5px;
  right: -5px;
  border-width: 0 50px 50px 0;
  border-color: transparent white transparent transparent;
}