/* Ensures the image is responsive */
.image {
    display: block;
    width: 100%;
    height: auto;
    transition: transform 0.3s ease; /* Adds a smooth transition for the transform property */
}

/* Defines the overlay effect */
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 15, 152, 0.3);
    opacity: 1;
    transition: opacity 0.8s ease; /* Smooth transition for opacity */
}

/* Adjusts the opacity of the overlay on hover */
.relatives:hover .overlay {
    opacity: 0;
    cursor: pointer;
}

/* Adds the zoom-in effect on hover */
.relatives:hover .image {
    cursor: pointer;
    transform: scale(1.1); /* Scales the image to 110% of its original size */
}

/* Ensures the zoomed image stays within the boundaries */
.relatives {
    position: relative;
    overflow: hidden;
}

/* Positions the content within the relatives container */
.content {
    position: absolute;
    bottom: 40px;
    left: 25px;
    right: 10px;
    color: white;
    text-align: left;
}

/* Manages the visibility of the additional information */
.additional-info {
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    transition: opacity 0.3s ease, max-height 0.3s ease;
}

/* Shows the additional information on hover */
.relatives:hover .additional-info {
    opacity: 1;
    max-height: 100px; /* Adjust as needed */
}
